/* Accessibility Improvements */
.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	border: 0;
}

[role="button"],
button {
	cursor: pointer;
}

/* Focus Visible Polyfill */
.js-focus-visible :focus:not(.focus-visible) {
	outline: none;
}

.js-focus-visible .focus-visible {
	outline: none;
	box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.6);
}
